import { createGlobalStyle } from 'styled-components'
// eslint-disable-next-line import/no-unresolved
import { PancakeTheme } from '@pancakeswap-libs/uikit'

declare module 'styled-components' {
  /* eslint-disable @typescript-eslint/no-empty-interface */
  export interface DefaultTheme extends PancakeTheme {}
}

const GlobalStyle = createGlobalStyle`
  * {
    font-family: 'Kanit', sans-serif;
  }
  body {
    background-color: ${({ theme }) => theme.colors.background};

    img {
      height: auto;
      max-width: 100%;
    }
  }
  .but1a {
    -webkit-box-align: center;
    align-items: center;
    background-color: rgb(118, 69, 217);
    border: 2px solid rgb(118, 69, 217);
    border-radius: 16px;
    color: #fff;
    display: inline-flex;
    font-size: 14px;
    font-weight: 400;
    height: 28px;
    line-height: 1.5;
    padding: 0px 8px;
    white-space: nowrap;
    float: right;
}
.but1abc {
  -webkit-box-align: center;
  align-items: center;
  background-color: transparent;
  border: 2px solid rgb(48, 168, 134);
  border-radius: 16px;
  color: rgb(48, 168, 134);
  display: inline-flex;
  font-size: 14px;
  font-weight: 400;
  height: 28px;
  line-height: 1.5;
  padding: 0px 8px;
  white-space: nowrap;
  float: right;
  margin-right: 10px;
}
.backbg{
  background-image: url('/images/web-img/Main_Background.png');
  background-size: cover;
  background-repeat: norepeat;
}
.btn1{
  align-items: center;
  background-color: #E3B93F;
  border: 0;
  border-radius: 16px;
  box-shadow: inset 0px -1px 0px rgba(14,14,44,0.4);
  color: #FFFFFF;
  cursor: pointer;
  display: -webkit-inline-box;
  display: -webkit-inline-flex;
  display: -ms-inline-flexbox;
  display: inline-flex;
  font-family: inherit;
  font-size: 16px;
  font-weight: 600;
  width: 100%;
  height: 48px;
  line-height: 1;
  -webkit-letter-spacing: 0.03em;
  -moz-letter-spacing: 0.03em;
  -ms-letter-spacing: 0.03em;
  letter-spacing: 0.03em;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  -ms-flex-pack: center;
  justify-content: center;
  outline: 0;
  padding: 0 15px;
  -webkit-transition: background-color 0.2s;
  transition: background-color 0.2s;
  opacity: 1;
  border: 2px solid #fff;
}
}
`

export default GlobalStyle
